.container {
    width: 100%;    
    max-width: 480px;
    margin: 0 auto;
    
    padding-left: 20px;
    padding-right: 20px;
    
    @include tab {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }
    @include pc {
        max-width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }    
}