@mixin transition($property: color) {
  transition: $property 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
@mixin transition-double($propertyOne, $propertyTwo: color) {
  transition: $propertyOne 250ms cubic-bezier(0.4, 0, 0.2, 1),
    $propertyTwo 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
@mixin transition-triple(
  $propertyOne,
  $propertyTwo: transform,
  $propertyThree: color
) {
  transition: $propertyOne 250ms cubic-bezier(0.4, 0, 0.2, 1),
    $propertyTwo 250ms cubic-bezier(0.4, 0, 0.2, 1),
    $propertyThree 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

@mixin grid($gap: 30px, $wrap: wrap) {
  display: flex;
  gap: $gap;
  flex-wrap: $wrap;
}
@mixin grid-item($cards, $gaps: $cards - 1) {
  width: calc((100% - 30px * $gaps) / $cards);
}

@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin retina {
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    @content;
  }
}

@mixin mob {
  @media screen and(min-width: 480px) {
    @content;
  }
}
@mixin tab {
  @media screen and(min-width: 768px) {
    @content;
  }
}
@mixin pc {
  @media screen and(min-width: 1200px) {
    @content;
  }
}
@mixin no-pc {
  @media screen and(max-width: 1199px) {
    @content;
  }
}
