.modal-form {
  margin: auto;
  max-width: 368px;
}

.modal-list {
  margin-bottom: 24px;

  @include pc {
    margin-bottom: 32px;
  }
}

.modal-input {
  width: 100%;
  height: 42px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;

  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;

  color: var(--secondary-text-color);
  outline: unset;
  padding: 11px;

  transition: border 250ms;

  &:focus {
    border: 2px solid var(--accent-color);
  }
}

.modal-item:not(:last-child) {
  margin-bottom: 16px;
}

.modal-comment {
  width: 100%;
  height: 84px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;

  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;

  outline: unset;
  padding: 11px;
  resize: none;
  display: block;
  &:focus {
    border: 2px solid var(--accent-color);
  }
}

.modal-comment::placeholder,
.modal-input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: var(--bg-color);
}

.modal-comment:focus {
  cursor: pointer;
}

.modal__btn {
  display: block;
  margin: auto;
}
