.footer {
  &__container {
    padding-top: 60px;
    @include tab {
      padding-top: 40px;
    }
    @include pc {
      padding-top: 120px;
    }
  }
  &__top {
    display: flex;
    flex-direction: column;
    gap: 60px;
    padding-bottom: 40px;

    border-bottom: 1px solid #e1e1e1;
    @include tab {
      flex-direction: row;
      justify-content: space-between;
      padding-bottom: 32px;
    }
    @include pc {
      padding-bottom: 60px;
    }
  }
  &__text {
    display: flex;
    flex-direction: column;
    gap: 16px;

    font-weight: 700;
    text-transform: uppercase;
    color: var(--primary-text-color);
  }
  &__text-start {
    max-width: 232px;
    font-size: 18px;
    @include tab {
      max-width: 352px;
      font-size: 26px;
    }
    @include pc {
      font-weight: 400;
      font-size: 30px;
    }
  }
  &__text-end {
    max-width: 200px;
    font-size: 34px;
    @include tab {
      font-size: 48px;
      letter-spacing: 0.06em;
    }
    @include pc {
      font-size: 58px;
    }
  }
  &__bottom {
    @include flex();
    padding-top: 20px;
    padding-bottom: 20px;
    @include tab {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }
  &__rights {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;

    color: #7a7a7a;
    @include tab {
      width: 298px;
      line-height: 1.86;
      letter-spacing: 0.02em;
    }
    @include pc {
      font-size: 12px;
    }
  }
}
.soc-footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;
  @include tab {
    gap: 32px;
    justify-content: end;
  }
  @include pc {
    gap: 40px;
    align-items: flex-end;
  }
  &__list {
    display: flex;
    flex-direction: row;
    gap: 20px;
    @include tab {
      gap: 30px;
    }
  }
  &__item {
    width: 34px;
    height: 34px;
  }
  &__link {
    @include flex();
    width: 100%;
    height: 100%;

    color: var(--bg-color);
    background-color: var(--accent-color);
    border-radius: 50%;

    @include transition-double(transform);

    &:hover,
    &:focus {
      color: var(--primary-text-color);
      transform: scale(1.05);
    }
  }
  &__tell {
    font-weight: 700;
    line-height: 1.86;
    color: var(--secondary-text-color);

    @include tab {
      font-size: 14px;
      width: 310px;
    }
    @include pc {
      font-size: 16px;
      letter-spacing: 0.02em;
      width: 341px;
    }
  }
  &__call {
    margin-left: 3px;
    color: var(--primary-text-color);
    @include transition-double(transform);

    &:hover,
    &:focus {
      color: var(--button-hover);
    }
  }
}
