:root {
  --secondary-font: "Seymour One", sans-serif;
  --primary-text-color: #fcfcfc;
  --secondary-text-color: #131313;
  --accent-color: #f8d24e;
  --button-hover: #0a8817;
  --bg-color: #84a178;
  --letter-spacing: .04em;
  --line-height-auto: 1.2;
  --text-line-height: 1.76;
}

p, h1, h2, h3 {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  fill: currentColor;
}

address {
  font-style: normal;
}

input, label:not([type="checkbox"]) {
  display: block;
}

body {
  letter-spacing: var(--letter-spacing);
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  line-height: var(--line-height-auto);
  background-color: var(--bg-color);
  color: var(--primary-text-color);
}

@media screen and (min-width: 768px) {
  body {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  body {
    font-size: 16px;
  }
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  outline-offset: -2px;
  border-radius: 5px;
  outline: 2px solid #e2b924;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

::-webkit-scrollbar-thumb:hover {
  background-color: #e2b924;
}

body.data-open-modal-buy-now, body.data-open-modal-franc, body.data-open-modal-loc, body.data-open-modal-read-more, body.data-open-modal-ingredients {
  overflow: hidden;
}

.btn {
  max-width: 196px;
  max-height: 42px;
  text-align: center;
  background-color: var(--accent-color);
  color: var(--secondary-text-color);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: none;
  align-items: center;
  padding: 12px 32px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.btn:hover {
  background-color: var(--button-hover);
  transform: translateY(-2px);
  box-shadow: 0 5px #087313;
}

.btn:focus {
  background-color: var(--button-hover);
  color: var(--primary-text-color);
}

.btn:active {
  transform: translateY(1px);
  box-shadow: 0 3px #087313;
}

.btn__icon {
  margin-left: 7px;
}

.btn-secondary {
  border-color: var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
  border-style: solid;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-secondary:hover {
  border-color: var(--button-hover);
  background-color: rgba(0, 0, 0, 0);
}

.btn-secondary:focus {
  border-color: var(--button-hover);
  color: var(--primary-text-color);
  background-color: rgba(0, 0, 0, 0);
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%)scale(.9);
}

.modal {
  width: calc(100% - 40px);
  max-width: 568px;
  background: var(--primary-text-color);
  border: 2px solid var(--accent-color);
  border-radius: 10px;
  padding: 28px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

